/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
 #wp-travel-enquiries{
    max-width: 550px;
    top: 0;
    background: #fff;
    padding: 0px;
    width: 100%;
    position: relative;
    display: block;
    border-radius: 5px;
    .wp-travel-form-field {
        margin: 10px 0;
        width: 100%;
        display: inline-block;
        label{
            width: 100%;
            display: block;
        }
        input{
            width: 100%;
        }
        input[type=checkbox]{
            width: auto;
        }
        textarea {
            width: inherit;
        }
    }
 }
